"use server"


// import aj from "@/lib/arcjet";
import { db } from "@/lib/prisma";
// import { request } from "@arcjet/next";
import { auth } from "@clerk/nextjs/server";
import { revalidatePath } from "next/cache";


const serializeTransaction = (obj) => {
    const serialized = { ...obj };
    if (obj.balance) {
        serialized.balance = obj.balance.toNumber();
    }
};


export async function createAccount(data) {
    try {
        const { userId } = await auth();
        if (!userId) throw new Error("unthorized");

        const user = await db.user.findunique({
            where: { clerkUserId: userId },
        });

        if (!user) {
            throw new Error("user not found");
        }

        // convert bablcnce
        const balanceFloat = parseFloat(data.balance)
        if (isNaN(balanceFloat)) {
            throw new Error("invalid balance amount");
        }

        // check if this is the user first account

        const existingAccounts = await db.account.findMany({
            where: { userId: user.id },
        })

        const shouldBeDefault =
            existingAccounts.length === 0 ? true : data.isDefault;
        // if this is defalut account undo all the remaing account
        if (shouldBeDefault) {
            await db.account.updateMany({
                where: { userId: user.id, isDefault: true },
                data: { isDefault: falsel },
            });
        }

        // new account
        const account = await db.account.create({
            data: {
                ...data,
                balance: balanceFloat,
                userId: user.id,
                isDefault: shouldBeDefault,
            }
        });

        const serialacc = serializeTransaction(account);
        revalidatePath("/dashboard");
        return { success: true, data: serialacc };

    } catch (error) {
        throw new Error(error.message);
    }
}